.mainDiv{
    height: 83vh;
    margin-top: unset;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}
.mainDiv>div:nth-of-type(1){
    text-align: center;
}
form{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 70vw;
}
form>div:nth-of-type(1){
    text-align: center;
    width: 100%;
    margin-bottom: 0.5%;
}
form>div:nth-of-type(2){
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;

}
input{
    margin: 1% 1%;
    border:none;
    outline: none;
    font-size: 1.6rem;
    border-bottom: 2px solid black;
    background-color: transparent;
   padding: 0 1%;
   border-radius: 10px;
}
.btn:active{
    transform: scale(0.7);
}
