.main{
    min-height: 83vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.tableDiv{
    width: 70vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.BtnDiv{
    width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1%;
    margin-top: 20px;
}
table{
    width: 80%;
    text-align: center;
}
.BtnDiv>button{
 padding: 1% 2%;
 border-radius: 10px;
 box-shadow: 2px 2px 5px black;
}
.BtnDiv>button:active{
    transform: scale(0.7);
}
.BtnDiv>button:hover{
 box-shadow: -2px -2px 5px black;

}
.naviDiv{
display: flex;; width: 50%;
justify-content: center;
align-items: center;
}
.naviDiv>div{
    margin: 0 20px; width: 30%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1rem;
    border-bottom: 2px dotted black;
    border-radius: 10px;
    padding: 0 1%;
}
.thead{
    border-bottom: 2px solid black;
    border-radius: 10px;
}