.mainDiv{
 display: flex;
 justify-content: space-evenly;
 align-items: center;
 flex-direction: column;
 padding: 0 3%;
}
.head{
    text-align: center;
    border-bottom: 1px solid black;
}
.fournewsContainer{
    width: 70%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}
.fournews{
    width: 48%;
}
.lastnews{
    width: 70%;
    margin: 0 auto;
}
.mainHeading{
    font-weight: 700;
    font-family: DauphinPlain;
    font-size: 3rem;
}