.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}


::-webkit-scrollbar-thumb {
  background: rgb(194, 192, 192);
}


::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.newsDiv{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 83vh;
  overflow-y: scroll;
  
}
.newsTab{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 20%;
  border: 2px solid black;
  padding-bottom: 1%;
  max-height: 500px;
  overflow-y: scroll;
  margin: 1%;
  box-shadow: 5px 5px 2px black;
}
.imgholder{
width: 100%;
height: 40%;
display: flex;
justify-content: space-between;
flex-direction: column;
text-align: end;
}
.imgholder>span{
padding: 0 2%;
font-size: 0.7rem;
font-weight: 500;
}
.imgDiv>img{
  width: 100%;
  height: 100%;
}
.headingHolder>h2{
  padding: 0 2%;
  font-size: 1.2rem;
  color: lightslategrey;
  text-shadow: 1px 1px 1px black;
}
.headingHolder>h4{
  padding: 0 2%;
  font-size: 0.8rem;
  color: #272d38c5;
}
.contentHolder{
  display: flex;
  justify-content: space-between;
}
.contentHolder>p{
 font-size: 1rem;
 color: black;
 font-weight: 500;
 margin-right: 1%;
}
.contentHolder>button{
  outline: none;
  border: none;
  background-color: darkgrey;
  padding: 2% 3%;
  box-shadow: 2px 2px 2px black;
  text-shadow: 1px 1px 1px black;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
